	module.exports = {"data":{"site":{"meta":{"title":"四条眉毛的博客","description":"I'm Lin Jin , a web developer and a travel enthusiasts. My target is to write clean and efficient code, to solve poblems on the web and to learn something more. ","author":"Lin Jin"}},"post":{"id":"/Users/fourfish/Desktop/gatsby-starter-bootstrap/src/pages/articles/2017-12-10-斯沃茨：不幸陷入政治的互联网之子/index.md absPath of file >>> MarkdownRemark","html":"<p>最近在忙着弄毕设，一开撕就遇到个大问题，没有内容。我喜欢旅游，喜欢阅读，喜欢一切有意义，有意思的事物。但是，我已经有了一个博客。我可以在上面畅所欲言，完全没必要再弄个网站，陈列些七七八八的事情，像个卖东西的网站。最后，经过一番思索，我决定做个有关知乎的网站。</p>\n<p>看到这里，你可能觉得我离题了，其实不是。我想说的是，我们这些程序员有时候太着迷技术了。我们搭建起供几亿人使用的网站，却只拿来卖商品。我们收集了数以万计的数据，到头来成了垄断的帮凶。究其原因，难道不是因为我们没有想法吗！互联网技术越来越强大，我们却只拿它干以前的事情，帮助某些人堆积财富。</p>\n<p>斯沃茨是知道如何使用互联网的人。可惜，他不知不觉间陷入了政治。</p>\n<p>一时兴起，正在完成中…</p>","frontmatter":{"layout":"post","title":"斯沃茨：不幸陷入政治的互联网之子","path":"斯沃茨：不幸陷入政治的互联网之子","categories":["所思所想"],"date":"2017/12/10"}}},"pathContext":{"path":"斯沃茨：不幸陷入政治的互联网之子"}}

